.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgba(71,71,71 );
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
}
.image{
    border-radius: 50%;
    height: 250px;
    width: 250px;
}